import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams ,Events} from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { WorkflowServiceProvider } from '../../../../providers/workflow-service';
import { ToastController } from 'ionic-angular';

/**
 * Generated class for the WorkflowroutePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-workflowroute',
  templateUrl: 'workflowroute.html',
})
export class WorkflowroutePage {
  public workflowinfo={};
  public bussid:string;
  public busstype:string;
  public wfid:string;
  public memo: string;
  public transid:string;
  public data={};
  public list=[];
  public status='';
  public falg = false;
  constructor(public navCtrl: NavController, public navParams: NavParams,
              public toastCtrl: ToastController,
              public httpService:HttpServicesProvider,
              public workflow:WorkflowServiceProvider,
              public events: Events) {
  }
  public  chekFun(i){
    let me=this;
    this.list.forEach(function(data,inde,array) {
      if(i==inde){
        data.chek=true;
        me.data=data;
      }else{
        data.chek=false
      }
    });
  }
  ionViewWillEnter(){
    this.memo='';
    this.bussid=this.navParams.get("bussid");
    this.busstype=this.navParams.get("busstype");
    this.wfid=this.navParams.get("wfid");
    this.workflowinfo['memo']='';
    this.workflowinfo['transid']='';
    this.initway();
    console.log(this.bussid);
    console.log(this.busstype);
    if(this.status=='CLOSE' || this.status=='CANCEL'){
      
    }
  }
  initway(){
    this.workflow.workflowWay(this.wfid,data=>{
      let rowData={};
      let chekVale;
      let me=this;
      for (var i = 0; i < data.length; i++) {
        if(i==0){
          chekVale=true;
          me.data=data[i];
        }else{
          chekVale=false;
        }
        rowData = {
          "key":data[i]["key"],
          "value":data[i]["value"],
          "chek":chekVale
        }
        this.list.push(rowData);
      }
    })
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad WorkflowroutePage');
  }
  workflowSend(){
    this.workflowinfo['wfid']=this.wfid;
    if(JSON.stringify(this.data) != "{}"){
      this.workflowinfo['transid']=this.data['key'];
    }
    this.workflowinfo['memo']=this.memo;
    this.falg = true;
    var url='/workflow/sendwf/'+this.busstype+'/'+this.bussid;
    this.httpService.postform(url,null,this.workflowinfo,(result)=>{
      let toast = null;
      if(result.code==0){
        console.log('发送成功');
        toast = this.toastCtrl.create({
          message: result.message,
          duration: 1000,
          position: 'middle'
        })
      }else{
        console.log('发送失败');
        toast = this.toastCtrl.create({
          message: result.message,
          duration: 1000,
          position: 'middle'
        })
      }
      toast.present();
      this.navCtrl.pop()
      this.events.publish('workflowPage',true,Date.now());

    })

  }
}



